<template>
  <list-page
    :title="$t('sign.forget.title')"
    :tip="$t('sign.forget.tip')"
    :button="$t('btn.next')"
    :disable="disable"
    @click="toNext"
  >
    <field
      v-model="email"
      :title="$t('sign.info.email')"
      :placeholder="$t('sign.info.email')"
      :error="error"
    />
  </list-page>
</template>

<script>
import ListPage from '@/components/list/ListPage.vue'
import Field from '@/components/list/Field.vue'

export default {
  components: {
    ListPage,
    Field
  },
  data () {
    return {
      email: '',
      error: ''
    }
  },
  computed: {
    disable () {
      return !this.email
    }
  },
  methods: {
    toNext () {
      if (Math.random() > .5) {
        this.error = 'some error'
      } else {
        this.$router.push({
          name: 'resetpwd'
        })
      }
    }
  }
}
</script>

